import React from 'react';

class Home extends React.Component {
    constructor( props ) {
        super( props );
        this.props = props;
        this.state = {
            content: ''
        }
    }

    componentDidMount() {
        fetch( 'content/home.json' )
            .then( response => response.json() )
            .then( data => {
                this.setState( { content: data, currentPage: this.props.page } );
            } );
    }

    componentDidUpdate() {
        if ( this.state.content === '' ) { this.setState( { content: this.props.content } ) }
    }

    render() {
        let windowContent = 'Loading...';
        if ( this.state.content !== '' ) {
            windowContent = this.state.content.content.map( ( item, key ) => (

                <p key={key}>{item}</p>


            ) )
        }
        return (
            <>
                <h4 className="content__title std-yellow">{this.state.content['title']}</h4>
                {windowContent}
            </>
        );
    }
}

export default Home;