import React from 'react';

class Contact extends React.Component {
    constructor( props ) {
        super( props );
        this.props = props;
        this.state = {
            content: ''
        }
    }

    componentDidUpdate() {
        if ( this.state.content === '' ) { this.setState( { content: this.props.content } ) }
    }

    render() {
        let windowContent = 'Loading...';
        if ( this.state.content !== '' ) {
            windowContent = this.state.content.content.map( ( item, key ) => (
                <tbody key={key}>
                    <tr>
                        <td>
                            <p>{item}</p>
                        </td>
                        <td>
                            <a className="contact-link std-blue" href={this.state.content.url[key]} > LINK</a>
                        </td>
                    </tr>
                </tbody>
            ) )
        }

        return (
            <>
                <h4 className="content__title std-yellow">{this.state.content['title']}</h4>
                <table>
                    {windowContent}
                </table>
            </>
        );
    }
}

export default Contact;