import React from 'react';

class Projects extends React.Component {
    constructor( props ) {
        super( props );
        this.props = props;
        this.state = {
            content: ''
        }
    }

    componentDidUpdate() {
        if ( this.state.content === '' ) { this.setState( { content: this.props.content } ) }
    }

    render() {
        let windowContent = 'Loading...';
        let languageContent = 'Loading languages...';

        if ( this.state.content !== '' ) {
            languageContent = this.state.content.languages.map( ( item, key ) => (
                <>
                    {item.split( "," ).map( ( item, key ) => (
                        <span className={item.toLowerCase() + " skill-span"} key={key}>{item}</span>

                    ) )}
                </>
            ) );

            windowContent = this.state.content.content.map( ( item, key ) => (

                <tbody key={key}>
                    <tr className="projects-table__header">
                        <th colSpan="3">
                            <a className="projects-link" href={this.state.content.url[key]}>
                                <span className="std-blue">{this.state.content.header[key]}</span>
                                <span>/</span>
                            </a>
                        </th>
                    </tr>
                    <tr>
                        <td>
                            <img className="projects-table__image" src={this.state.content.image[key]} alt="project-thumbnail" />
                        </td>
                        <td className="projects-table__center">{item.split( "|" ).map( ( elem, key2 ) => (
                            <p key={key2} className="elem">{elem}</p>
                        ) )}
                        </td>
                        <td>
                            {languageContent[key]}
                        </td>
                    </tr>
                </tbody>

            ) )
        }

        return (
            <>
                <h4 className="content__title std-yellow">{this.state.content['title']}</h4>
                {/* <p>{this.state.content['content']}</p> */}
                <table className="projects-table">
                    {windowContent}
                </table>
            </>
        );
    }
}

export default Projects;