import React from 'react';

class Skills extends React.Component {
    constructor( props ) {
        super( props );
        this.props = props;
        this.state = {
            content: ''
        }
    }

    componentDidUpdate() {
        if ( this.state.content === '' ) { this.setState( { content: this.props.content } ) }
    }

    /*   componentWillUnmount() {
          this.setState( { content: '' } )
      }
   */

    render() {
        let windowContent = 'Loading...';
        if ( this.state.content !== '' ) {
            windowContent = this.state.content.content.map( ( item, key ) => (
                ( item.indexOf( '<' ) === -1 ) ?
                    <p key={key} style={{
                        marginLeft: "4rem"
                    }} className="content__list" > {item}</p>
                    :
                    <p key={key} className="content__list std-blue" > {item}</p>

            ) )
        }
        return (
            <>
                <h4 className="content__title std-yellow">{this.state.content['title']}</h4>
                {windowContent}
            </>
        );
    }
}

export default Skills;