import React from 'react';

class About extends React.Component {
    constructor( props ) {
        super( props );
        this.props = props;
        this.state = {
            content: ''
        }
    }

    componentDidUpdate() {
        if ( this.state.content === '' ) { this.setState( { content: this.props.content } ) }
    }

    render() {
        let windowContent = 'Loading...';
        if ( this.state.content !== '' ) {
            windowContent = this.state.content.content.map( ( item, key ) => (

                <p key={key}>{item}</p>

            ) )
        }

        return (
            <>
                <h4 className="content__title std-yellow">{this.state.content['title']}</h4>
                <div style={{ marginBottom: '1rem' }}>
                    <span className="window__commandLineText">Website stack: </span><span className="skill-span react">REACT</span><span className="skill-span scss">SCSS</span><span className="skill-span html">HTML</span>
                </div>
                <div>{windowContent}</div>
            </>
        );
    }
}

export default About;